<template>
	<div class="body fundManage_body">
		<div class="main under_fund">
			<div class="under_fund_main table">
				<el-table ref="multipleTable" :data="tableData" border style="width: 100%">
					<el-table-column prop="index" label="序号" width="100" fixed>
						<template slot-scope="scope">
							<div class="" v-if="scope.row.id">
								{{(currentPage-1)*pageSize + scope.$index }}
							</div>
							<div class="" v-else>
								新增
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="fund_type" label="投资人名称" width="150">
						<template slot-scope="scope">
							<div class="" v-if="scope.row.id">
								<div class="" v-if="scope.row.id == edit_id">
									<el-select v-model="edit_investor_id" @change="investTypeChangeEdit($event,scope)" filterable clearable placeholder="请选择">
									    <el-option v-for="item in investor_list_edit" :key="item.id" :label="item.name" :value="item.id">
									    </el-option>
									</el-select>
								</div>
								<div class="" v-else>
									{{ scope.row.investor_name }}
								</div>
							</div>
							<div class="" v-else>
								<el-select v-model="investor_id" filterable @change="investTypeChange($event)" clearable placeholder="请选择">
								    <el-option v-for="item in investor_list" :key="item.id" :label="item.name" :value="item.id">
								    </el-option>
								</el-select>
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="investor_class" label="投资人类别" width="200">
						<template slot-scope="scope">
							<div class="" v-if="scope.row.investor_class == 1">
								管理公司 
							</div>
							<div class="" v-else-if="scope.row.investor_class == 2">
								一般投资人
							</div>
							<div class="" v-else-if="scope.row.investor_class == 3">
								母基金
							</div>
							<div class="" v-else-if="scope.row.investor_class == 4">
								子基金
							</div>
							<div class="" v-else-if="scope.row.investor_class == 5">
								自管基金
							</div>
							<div class="" v-else>
								
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="address" label="是否为GP" width="115">
						<template slot-scope="scope">
							<div class="" v-if="scope.row.id">
								<!-- <div class="" v-if="scope.row.id == edit_id">
									<el-select v-model="edit_is_gp" clearable placeholder="请选择">
									    <el-option v-for="item in is_gp_list" :key="item.value" :label="item.label" :value="item.value">
									    </el-option>
									</el-select>
								</div> -->
								<!-- <div class="" v-else> -->
								<div class="">
									{{ scope.row.is_gp?'是':'否' }}
								</div>
							</div>
							<div class="" v-else>
								<!-- <el-select v-model="is_gp" clearable placeholder="请选择">
								    <el-option v-for="item in is_gp_list" :key="item.value" :label="item.label" :value="item.value">
								    </el-option>
								</el-select> -->
								<div class="">
									{{ is_gp?'是':'否' }}
								</div>
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="invest_date" label="入伙日期" width="180">
						<template slot-scope="scope">
							<div class="" v-if="scope.row.id">
								<div class="" v-if="scope.row.id == edit_id">
									<el-date-picker v-model="edit_invest_date" type="date" :editable='false' value-format="yyyy-MM-dd" placeholder="选择日期">
    								</el-date-picker>
								</div>
								<div class="" v-else>
									{{ scope.row.invest_date?scope.row.invest_date:'-' }}
								</div>
							</div>
							<div class="" v-else>
								<el-date-picker v-model="invest_date" type="date" :editable='false'  value-format="yyyy-MM-dd" placeholder="选择日期">
    								</el-date-picker>
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="subscribe" label="认缴（万元）" width="180">
						<template slot-scope="scope">
							<div class="" v-if="scope.row.id">
								<div class="" v-if="scope.row.id == edit_id">
									<el-input placeholder="请输入内容" v-model="edit_subscribe" clearable maxlength='16'>
									</el-input>
								</div>
								<div class="" v-else>
									{{ scope.row.subscribe }}
								</div>
							</div>
							<div class="" v-else>
								<el-input placeholder="请输入内容" v-model="subscribe" clearable maxlength='16'>
								</el-input>
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="service_fee" label="应收服务费（万元）" width="180">
						<template slot-scope="scope">
							<div class="" v-if="scope.row.id">
								<div class="" v-if="scope.row.id == edit_id">
									<el-input placeholder="请输入内容" v-model="edit_service_fee" clearable maxlength='16'>
									</el-input>
								</div>
								<div class="" v-else>
									{{ scope.row.service_fee }}
								</div>
							</div>
							<div class="" v-else>
								<el-input placeholder="请输入内容" v-model="service_fee" clearable maxlength='16'>
								</el-input>
							</div>
						</template>
					</el-table-column>
					
					<el-table-column prop="address" label="是否冻结" width="115">
						<template slot-scope="scope">
							<div class="" v-if="scope.row.id">
								<div class="" v-if="scope.row.id == edit_id">
									<el-select v-model="edit_is_freeze" clearable placeholder="请选择">
									    <el-option v-for="item in is_freeze_list" :key="item.value" :label="item.label" :value="item.value">
									    </el-option>
									</el-select>
								</div>
								<div class="" v-else>
									{{ scope.row.is_freeze?'是':'否' }}
								</div>
							</div>
							<div class="" v-else>
								<el-select v-model="is_freeze" clearable placeholder="请选择">
								    <el-option v-for="item in is_freeze_list" :key="item.value" :label="item.label" :value="item.value">
								    </el-option>
								</el-select>
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="address" label="工商状态" width="180">
						<template slot-scope="scope">
							<div class="" v-if="scope.row.id">
								{{ scope.row.is_change?'待变更':'正常' }}
							</div>
							<div class="" v-else>
								
							</div>
						</template>
					</el-table-column>
					
					<el-table-column prop="ir_manager" label="IR经理" width="180">
					</el-table-column>
					<el-table-column prop="customer_manager" label="客户经理" width="180">
					</el-table-column>
					<el-table-column prop="unit" label="币种" width="140">
						<template slot-scope="scope">
							<div class="" v-if="scope.row.unit == 1">
								人民币
							</div>
							<div class="" v-else-if="scope.row.unit == 2">
								美元
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="current_value" label="现值（万元）" width="150">
					</el-table-column>
					<el-table-column prop="amount_paid_in" label="实缴（万元）" width="120">
					</el-table-column>
					<el-table-column prop="amount_paid_in_plan" label="实缴进度" width="200">
						<template slot-scope="scope">
							<div class="" v-if="scope.row.id">
								{{ scope.row.amount_paid_in_plan }}&nbsp;%
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="back" label="回款返本（万元）" width="150">
					</el-table-column>
					<el-table-column prop="income" label="回款收益-税后（万元）" width="180">
					</el-table-column>
					<el-table-column prop="irr" label="XIRR" width="150">
					</el-table-column>
					<el-table-column prop="interest" label="计息中（万元）" width="150">
					</el-table-column>
					<el-table-column prop="is_check" label="已确认（万元）" width="150">
					</el-table-column>
					<el-table-column fixed="right" label="操作" width="180">
						<template slot-scope="scope">
							<div class="" v-if="scope.row.id">
								<div class="" v-if="scope.row.id != edit_id">
									<el-button type="text" size="small" @click="edit(scope.row)">编辑</el-button>
									<el-button type="text" size="small" @click="deleteInvestor(scope.row)">删除</el-button>
								</div>
								<div class="" v-else>
									<el-button v-if="scope.row.id == edit_id" type="text" size="small" @click="editSave(scope.row)">保存</el-button>
									<el-button v-if="scope.row.id == edit_id" type="text" size="small" @click="editCancel(scope.row)">取消</el-button>
								</div>
								
							</div>
							<div class="" v-else>
								<el-button type="text" size="small" @click="addMotherFundInvestor(scope)">保存</el-button>
								<el-button type="text" size="small" @click="clearData()">清除</el-button>
							</div>
						</template>
					</el-table-column>
				</el-table>
				<el-pagination v-if="totalCount" @size-change="everyPageSizeChange" prev-text="上一页" next-text="下一页" background @current-change="changeCurrentPage" :current-page="currentPage" :page-size="pageSize" layout="sizes, prev, pager, next, jumper" :total="totalCount">
    				</el-pagination>
			</div>
		</div>

	</div>
</template>

<script>
	import { dialogConfirm, dialogConfirmHasbtn,isValidMoneyLength } from '../../config/mUtils'
	import { getMotherFundInvestorListApi,getMotherFundInvestorAddInfoApi,addMotherFundInvestorApi,editMotherFundInvestorApi,getSelfFundInvestorListApi,getSelfFundInvestorAddInfoApi,addSelfFundInvestorApi,editSelfFundInvestorApi,deleteMotherFundInvestorApi,deleteSelfFundInvestorApi } from '../../config/api'

	export default {
		name: 'motherFundRunInvestor',
		data() {
			return {
				type: 1,//1代表母基金；2代表自管基金
				id: '',
				tableData: [],
				currentPage: 1,
				pageSize: 10,
				totalCount: 0,
				addData: {
					id: 0,
					unit: 3,
				},
				investor_list: [],
				investor_list_edit: [],
				//添加新数据的字段开始
				investor_id: '',
				invest_date: '',
				subscribe: '',
				service_fee: '',
				is_change: 1,
				is_freeze: 0,
				is_gp: 0,
				// 添加新数据的字段结束
				//编辑保存接口字段开始
				edit_id: '',
				edit_investor_id: '',
				edit_invest_date: '',
				edit_subscribe: '',
				edit_service_fee: '',
				edit_is_change: '',
				edit_is_freeze: '',
				edit_is_gp: '',
				//编辑保存接口字段结束
				investor_type: [
					{
						value: 1,
						label: '母基金'
					},
					{
						value: 2,
						label: '管理公司'
					},
					{
						value: 3,
						label: '投资人'
					}
				],
				business_status_list: [
					{
						value: 1,
						label: '待变更'
					}
				],
				is_freeze_list: [
					{
						value: 1,
						label: '是'
					},
					{
						value: 0,
						label: '否'
					},
				],
				is_gp_list: [
					{
						value: 1,
						label: '是'
					},
					{
						value: 0,
						label: '否'
					},
				],
			}
		},
		created() {
			this.id = this.$route.query.id;
			this.type = this.$route.query.type;
			this.getMotherFundInvestorList()
			this.getMotherFundInvestorAddInfo()
		},
		mounted() {},
		components: {},
		methods: {
			getMotherFundInvestorList(){
				if (this.type == 1) {
					getMotherFundInvestorListApi({
						fof_id: this.id,
						page: this.currentPage,
						size: this.pageSize
					}).then(res => {
						if(res.data.msgNo == 1) {
							this.tableData = res.data.payload.data;
							this.totalCount = res.data.payload.count;
							this.tableData.unshift(this.addData);
						} else {
							dialogConfirm("info", res.data.message);
						}
					})
				} else{
					getSelfFundInvestorListApi({
						pet_fund_id: this.id,
						page: this.currentPage,
						size: this.pageSize
					}).then(res => {
						if(res.data.msgNo == 1) {
							this.tableData = res.data.payload.data;
							this.totalCount = res.data.payload.count;
							this.tableData.unshift(this.addData);
						} else {
							dialogConfirm("info", res.data.message);
						}
					})
				}
			},
			getMotherFundInvestorAddInfo(){
				if (this.type == 1) {
					getMotherFundInvestorAddInfoApi({
						fof_id: this.id,
					}).then(res => {
						if(res.data.msgNo == 1) {
							this.investor_list = res.data.payload.investors;
						} else {
							dialogConfirm("info", res.data.message);
						}
					})
				} else{
					getSelfFundInvestorAddInfoApi({
						pet_fund_id: this.id,
					}).then(res => {
						if(res.data.msgNo == 1) {
							this.investor_list = res.data.payload.investors;
						} else {
							dialogConfirm("info", res.data.message);
						}
					})
				}
			},
			getMotherFundInvestorAddInfoEdit(){
				getMotherFundInvestorAddInfoApi({
					fof_id: this.id,
					investor_id: this.edit_investor_id
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.investor_list_edit = res.data.payload.investors;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			addMotherFundInvestor(){
				if(this.investor_id&&this.is_freeze!==''&&this.is_gp!==''){
					if(isValidMoneyLength(parseInt(this.subscribe))){
						dialogConfirm('info','认缴金额过大!');
						return;
					}
					if (parseFloat(this.subscribe) <= 0) {
						dialogConfirm('info','认缴金额应大于0!');
						return;
					}
					if (parseFloat(this.subscribe) != this.subscribe) {
						dialogConfirm('info','认购金额不能为空');
						return;
					}
					if (parseFloat(this.service_fee) < 0) {
						dialogConfirm('info','应收服务费应大于或等于0!');
						return;
					}
					if (parseFloat(this.service_fee) != this.service_fee) {
						dialogConfirm('info','应收服务费不能为空');
						return;
					}
					if(isValidMoneyLength(parseInt(this.service_fee))){
						dialogConfirm('info','应收服务费金额过大!');
						return;
					}
					if (this.type == 1) {
						addMotherFundInvestorApi({
							fof_id: this.id,
							investor_id: this.investor_id,
							invest_date: this.invest_date,
							service_fee: this.service_fee,
							subscribe: this.subscribe,
							is_change: this.is_change,
							is_freeze: this.is_freeze,
							is_gp: this.is_gp
						}).then(res => {
							if(res.data.msgNo == 1) {
								dialogConfirm("success", res.data.message);
								this.investor_id = '';
								this.invest_date = '';
								this.service_fee = '';
								this.subscribe = '';
								this.is_change = '';
								this.is_freeze = 0;
								this.is_gp = 0;
								this.getMotherFundInvestorAddInfo()
								this.getMotherFundInvestorList()
							} else {
								dialogConfirm("info", res.data.message);
							}
						})
					} else{
						
						addSelfFundInvestorApi({
							pet_fund_id: this.id,
							investor_id: this.investor_id,
							invest_date: this.invest_date,
							service_fee: this.service_fee,
							subscribe: this.subscribe,
							is_change: this.is_change,
							is_freeze: this.is_freeze,
							is_gp: this.is_gp
						}).then(res => {
							if(res.data.msgNo == 1) {
								dialogConfirm("success", res.data.message);
								this.investor_id = '';
								this.invest_date = '';
								this.service_fee = '';
								this.subscribe = '';
								this.is_change = '';
								this.is_freeze = 0;
								this.is_gp = 0;
								this.getMotherFundInvestorAddInfo()
								this.getMotherFundInvestorList()
							} else {
								dialogConfirm("info", res.data.message);
							}
						})
					}
				}else{
					if(!this.investor_id){
						dialogConfirm("info", '投资人名称不能为空!');
					}else if(this.is_gp === ''){
						dialogConfirm("info", '是否为GP不能为空!');
					}else if(this.is_freeze === '' ){
						dialogConfirm("info", '冻结选项不能为空!');
					}
				}
				
				
			},
			
			investTypeChange(e,a){
				if (e) {
					var item = this.getSelectedItem(e,this.investor_list);
					this.tableData[0].investor_type = item.investor_class
				} else {
					this.tableData[0].investor_type = '';
				}
			},
			investTypeChangeEdit(e,a){
				var index = a.$index;
				if (e) {
					var item = this.getSelectedItem(e,this.investor_list_edit);
					this.tableData[index].investor_type = item.investor_class;
				} else {
					this.tableData[index].investor_type = '';
				}
			},
			getSelectedItem(e,data){
				let obj = {};
			    obj = data.find((item)=>{
				    		return item.id === e;
				    	  });
				return obj
			},
			edit(e){
				console.log(e);
				this.edit_id = e.id;
				this.edit_investor_id = e.investor_id;
				this.edit_invest_date = e.invest_date;
				this.edit_subscribe = e.subscribe;
				this.edit_service_fee = e.service_fee;
				this.edit_is_change = e.is_change;
				this.edit_is_freeze = e.is_freeze;
				this.edit_is_gp = e.is_gp;
				this.getMotherFundInvestorAddInfoEdit()
			},
			editSave(e){
				if(this.edit_investor_id&&this.edit_is_gp!==''&&this.edit_is_freeze!==''){
					if(isValidMoneyLength(parseInt(this.edit_subscribe))){
						dialogConfirm('info','认缴金额过大!');
						return;
					}
					if (parseFloat(this.edit_subscribe) <= 0) {
						dialogConfirm('info','认缴金额应大于0!');
						return;
					}
					if (parseFloat(this.edit_subscribe) != this.edit_subscribe) {
						dialogConfirm('info','认缴金额不能为空');
						return;
					}
					if (parseFloat(this.edit_service_fee) < 0) {
						dialogConfirm('info','应收服务费应大于或等于0!');
						return;
					}
					if (parseFloat(this.edit_service_fee) != this.edit_service_fee) {
						dialogConfirm('info','应收服务费不能为空');
						return;
					}
					if(isValidMoneyLength(parseInt(this.edit_service_fee))){
						dialogConfirm('info','应收服务费金额过大!');
						return;
					}
					if (this.type == 1) {
						editMotherFundInvestorApi({
							fof_id: this.id,
							investor_id: this.edit_investor_id,
							invest_date: this.edit_invest_date,
							subscribe: this.edit_subscribe,
							service_fee: this.edit_service_fee,
							is_change: this.edit_is_change,
							is_freeze: this.edit_is_freeze,
							is_gp: this.edit_is_gp,
							id: this.edit_id
						}).then(res => {
							// console.log(res);
							if(res.data.msgNo == 1) {
								dialogConfirm("success", res.data.message);
								this.edit_id = -1;
								this.getMotherFundInvestorList()
							} else {
								dialogConfirm("info", res.data.message);
							}
						})
					} else{
						editSelfFundInvestorApi({
							pet_fund_id: this.id,
							investor_id: this.edit_investor_id,
							invest_date: this.edit_invest_date,
							subscribe: this.edit_subscribe,
							service_fee: this.edit_service_fee,
							is_change: this.edit_is_change,
							is_freeze: this.edit_is_freeze,
							is_gp: this.edit_is_gp,
							id: this.edit_id
						}).then(res => {
							// console.log(res);
							if(res.data.msgNo == 1) {
								dialogConfirm("success", res.data.message);
								this.edit_id = -1;
								this.getMotherFundInvestorList()
							} else {
								dialogConfirm("info", res.data.message);
							}
						})
					}
				}else{
					if(!this.edit_investor_id){
						dialogConfirm("info", '投资人名称不能为空!');
					}else if(this.edit_is_gp === ''){
						dialogConfirm("info", '是否为GP不能为空!');
					}else if(this.edit_is_freeze === '' ){
						dialogConfirm("info", '冻结选项不能为空!');
					}
				}
				
				
			},
			editCancel(){
				this.edit_id = -1;
			},
			deleteInvestor(row){
				// console.log(row);
				if (row.can_del) {
					let message = "确定要删除所选投资人吗";
					dialogConfirmHasbtn(message).then(() => {
						if(this.type == 1){
							deleteMotherFundInvestorApi({
								id: row.id,
							}).then(res => {
								if (res.data.msgNo == 1) {
									dialogConfirm("success", res.data.message);
									let length = this.tableData.length;
									if(length > 1){
										this.getMotherFundInvestorList();
									}else{
										if(this.currentPage > 1){
											this.currentPage = this.currentPage - 1;
										}
										this.getMotherFundInvestorList();
									}
									// 重新获取新增时投资人列表
									this.getMotherFundInvestorAddInfo()
								}else{
									dialogConfirm("info", res.data.message);
								}
							})
						} else {
							deleteSelfFundInvestorApi({
								id: row.id,
							}).then(res => {
								if (res.data.msgNo == 1) {
									dialogConfirm("success", res.data.message);
									let length = this.tableData.length;
									if(length > 1){
										this.getMotherFundInvestorList();
									}else{
										if(this.currentPage > 1){
											this.currentPage = this.currentPage - 1;
										}
										this.getMotherFundInvestorList();
									}
									// 重新获取新增时投资人列表
									this.getMotherFundInvestorAddInfo()
								}else{
									dialogConfirm("info", res.data.message);
								}
							})
						}
					}).catch(() => {});
				} else{
					dialogConfirm("info", '您无权限操作该条数据');
				}
			},
			clearData(){
				this.investor_id = '';
				this.invest_date = '';
				this.subscribe = '';
				this.service_fee = '';
				this.is_change = '';
				this.is_freeze = '';
				this.is_gp = '';
			},
			// 分页功能模块
			everyPageSizeChange(e){
				this.pageSize = e;
				this.getMotherFundInvestorList()
			},
			changeCurrentPage(e){
				this.currentPage = e;
				this.getMotherFundInvestorList()
			},
			
			

		},
	}
</script>

<style scoped="scoped">
	.body {
		padding: 0 0px 30px 0px !important;
	}
	
</style>


